import { useTransactionList } from '@/hook/useTransactionList'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { NoData, SingleCoinImage } from '@cetus/ui-kit'
import Icon from '@cetus/ui-kit/src/components/Icon'
import { formatDateTime } from '@cetus/utils'
import {
  Center,
  Flex,
  HStack,
  Image,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Spinner,
  Text,
  VStack
} from '@chakra-ui/react'
import { useEffect, useMemo } from 'react'
import { AddressUnderline } from '../common'

interface RecentTransProps {
  isOpen: boolean
  onClose: () => void
}

function RecentTransactions({ isOpen, onClose }: RecentTransProps) {
  const { isApp } = useWindowWidth()

  const { currentAccount } = useAccountStore()

  const { getWalletHis, historyLoading, historyList, nextCursor } = useTransactionList()

  const getList = () => {
    if (currentAccount?.address) {
      getWalletHis(pageList, currentAccount?.address, nextCursorPage)
    }
  }

  useEffect(() => {
    if (currentAccount?.address) {
      getList()
    }
  }, [currentAccount?.address])

  const pageList = useMemo(() => {
    return historyList
  }, [historyList])
  console.log('üöÄ ~ pageList ~ historyList:', pageList, pageList?.length == 0, isOpen)

  const nextCursorPage = useMemo(() => {
    return nextCursor
  }, [nextCursor])

  return (
    <>
      <Modal autoFocus={false} returnFocusOnClose={false} isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay />
        <ModalContent minW={isApp ? '100%' : '700px'}>
          <ModalHeader fontWeight="500">Recent Transactions</ModalHeader>
          <ModalCloseButton />
          <ModalBody p="0">
            <VStack w="100%" align="flex-start" gap="0">
              {!isApp && <PoweredAndViewAll isApp={isApp} />}
              <VStack w="100%" gap="0">
                {!isApp && pageList?.length > 0 && (
                  <HStack w="100%" p="20px 16px">
                    <Text w="40%">Activity</Text>
                    <Text w="60%">Assets</Text>
                  </HStack>
                )}
                <VStack
                  w="100%"
                  p={!isApp ? '0px 16px' : '0px 8px'}
                  mb="20px"
                  align="flex-start"
                  h={pageList?.length > 4 ? '550px' : 'unset'}
                  overflow="auto"
                >
                  {pageList?.length == 0 ? (
                    <Flex w="100%" align="center" justifyContent="center" h="307px" mt="20px">
                      {historyLoading ? <Spinner /> : <NoData type="nodata" noBorder />}
                    </Flex>
                  ) : (
                    pageList?.map((item: any) => {
                      return <TransInfo key={item?.digest} trans={item} isApp={isApp} />
                    })
                  )}
                  {nextCursorPage &&
                    (historyLoading ? (
                      <Center w="200px" margin="auto" textAlign="center" pb="16px" pt="8px">
                        <Spinner size="sm" />
                      </Center>
                    ) : (
                      <Text w="200px" margin="auto" textAlign="center" cursor="pointer" mt="12px" h="32px" color="primary" onClick={getList}>
                        Load More
                      </Text>
                    ))}
                </VStack>
                {isApp && <PoweredAndViewAll isApp={isApp} />}
              </VStack>
            </VStack>
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  )
}

const TransInfo = (props: { trans: any; isApp: boolean }) => {
  const { trans, isApp } = props
  const { getAmount, getImage, getType } = useTransactionList()
  return (
    <HStack
      display="flex"
      flexDirection={isApp ? 'column' : 'row'}
      w="100%"
      bg="bg_primary"
      border="1px solid"
      borderColor="border"
      borderRadius="12px"
      p="20px"
    >
      <VStack w={!isApp ? '40%' : '100%'} align="flex-start">
        <HStack w="100%">
          <Image w="32px" h="32px" src={getImage(trans.type)} />
          <VStack w="100%" align="flex-start" gap="4px">
            <Text color="text_caption">{getType(trans.type)}</Text>
            {isApp && <TimeBlock timestampMs={trans?.timestampMs} />}
          </VStack>
        </HStack>
        {!isApp && <TimeBlock timestampMs={trans?.timestampMs} />}
        {!isApp && <AddressBlock tx={trans?.digest} />}
      </VStack>
      <VStack w={!isApp ? '60%' : '100%'} align="flex-start">
        {isApp && <Text mt="8px">Assets</Text>}
        {trans.coinChanges.map((coinChange: any, index: string) => {
          console.log('üöÄ ~ {trans.coinChanges.map ~ coinChange:', coinChange)
          return (
            <VStack w="100%" key={coinChange?.coinAddress + index} align="flex-start">
              <HStack justify="flex-start">
                <SingleCoinImage w="24px" h="24px" imageUrl={coinChange?.logo} />
                <Text color={coinChange.amount.indexOf('-') > -1 ? 'text_caption' : 'primary_green'}>
                  {getAmount(coinChange.amount, coinChange.decimal)}
                </Text>
                <Text>{coinChange.symbol}</Text>
              </HStack>
            </VStack>
          )
        })}
      </VStack>
      {isApp && (
        <VStack w="100%" align="flex-start">
          <Text mt="8px">Txns</Text>
          <AddressBlock tx={trans?.digest} />
        </VStack>
      )}
    </HStack>
  )
}

const TimeBlock = (props: { timestampMs: number }) => {
  const { timestampMs } = props
  return <Text>{formatDateTime(timestampMs - 8 * 60 * 60 * 1000)}&nbsp;UTC</Text>
}
const AddressBlock = (props: { tx: string }) => {
  const { tx } = props
  return (
    <HStack>
      <Image w="20px" src="/images/icon_suivision@2x.png" />
      <AddressUnderline
        address={tx}
        fontSize="14px"
        onClickLink={() => {
          // window.open(getExplorerUrl(tx, 'tx'), '_blank')
          window.open(`https://suivision.xyz/txblock/${tx}?tab=Activity&type=Cetus`, '_blank')
        }}
      />
    </HStack>
  )
}

const PoweredAndViewAll = (props: { isApp: boolean }) => {
  const { isApp } = props
  const { currentAccount } = useAccountStore()
  return (
    <HStack
      display="flex"
      flexDirection={isApp ? 'column-reverse' : 'row'}
      align="flex-start"
      p="0px 16px 16px"
      w="100%"
      justify="space-between"
      borderBottom="1px solid"
      borderColor="border"
      borderRadius="0 0 8px 8px"
    >
      <HStack>
        <Text>Powered by</Text>
        <Image w="20px" src="/images/icon_suivision@2x.png" />
        <Text>SuiVision</Text>
      </HStack>
      <HStack
        sx={{
          cursor: 'pointer',
          mr: '-4px',
          _hover: {
            p: {
              color: 'text_caption'
            },
            svg: {
              fill: 'text_caption'
            }
          }
        }}
        onClick={() => {
          window.open(`https://suivision.xyz/account/${currentAccount?.address}?tab=Activity&type=Cetus`, '_blank')
        }}
      >
        <Text>View all transactions</Text>
        <Icon xlinkHref="#icon-icon_link1" variant="gray" />
      </HStack>
    </HStack>
  )
}

export default RecentTransactions
