import useGlobalStore from '@cetus/stores/src/global'
import { useConnectWallet, useWallets } from '@mysten/dapp-kit'
import { useEffect } from 'react'

export default function usePhantomAutoConnect() {
  const { phantomInstance } = useGlobalStore()
  const { mutate: connect } = useConnectWallet()
  const supportWalletList = useWallets()

  const autoConnectPhantom = () => {
    const suiDappKitLocalData = JSON.parse(window?.localStorage?.getItem('sui-dapp-kit:wallet-connection-info') || '{}')
    const lastConnectedWalletName = suiDappKitLocalData?.state?.lastConnectedWalletName

    if (lastConnectedWalletName === 'Phantom') {
      const walletObj = supportWalletList.find(item => item.name === 'Phantom')
      if (walletObj) {
        connect({ wallet: walletObj })
      }
    }
  }

  useEffect(() => {
    if (phantomInstance) {
      autoConnectPhantom()
    }
  }, [phantomInstance])
}
