import icon_cancel from '@/assets/images/icon_cancel@2x.png'
import icon_createlimitorder from '@/assets/images/icon_createlimitorder@2x.png'
import icon_liquidityincreased from '@/assets/images/icon_liquidityincreased@2x.png'
import icon_liquidityremoved from '@/assets/images/icon_liquidityremoved@2x.png'
import icon_presalesettled from '@/assets/images/icon_presalesettled@2x.png'
import icon_redeem from '@/assets/images/icon_redeem@2x.png'
import icon_swap from '@/assets/images/icon_swap@2x.png'
import { formatNumber, fromDecimalsAmountFix } from '@cetus/utils'
import { useState } from 'react'

export function useTransactionList() {
  const [nextCursor, setNextCursor] = useState('')
  const [historyList, seHistoryList] = useState([])
  const [historyLoading, seHistoryLoading] = useState(true)

  const getWalletHis = async (pageList: any, walletAddress: string, nextCursor: string | null) => {
    seHistoryLoading(true)
    try {
      const response: any = await fetch(
        `https://api.blockvision.org/v2/sui/account/activities?address=${walletAddress}&protocol=cetus amm&cursor=${nextCursor}`,
        {
          method: 'GET',
          headers: {
            accept: 'application/json',
            'x-api-key': '2pQ56nlGm5vHmNdH5sqFpEGPENU'
          }
        }
      )
      const result = await response.json()
      const list = result?.result?.data || []
      const listSort = list.map((item: any) => {
        const sortedCoinChanges = item.coinChanges ? [...item.coinChanges] : [] // ÂàõÂª∫ coinChanges ÁöÑÂâØÊú¨
        sortedCoinChanges.sort((a, b) => parseFloat(b.amount) - parseFloat(a.amount)) // Ê†πÊçÆ amount Â±ûÊÄßÊéíÂ∫è
        return { ...item, coinChanges: sortedCoinChanges } // ËøîÂõûÂåÖÂê´ÊéíÂ∫èÂêé coinChanges ÁöÑÂØπË±°ÂâØÊú¨
      })
      setNextCursor(result?.result?.nextPageCursor)
      seHistoryList(pageList?.concat(listSort))
      seHistoryLoading(false)
    } catch (e) {
      console.log(e, 'error')
      seHistoryLoading(false)
    }
  }
  const getAmount = (amount: string, decimal: number) => {
    console.log('üöÄ ~ getAmount ~ decimal:', amount, decimal)
    const result = fromDecimalsAmountFix(amount, decimal).toString()
    const minusSign = result.replace('-', '')
    // if (Number(minusSign) > 1000000) {
    //  return amount?.indexOf('-') > -1 ? `-${formatNumberWithKMB(minusSign, decimal)}` : `+${formatNumberWithKMB(minusSign, decimal)}`
    // } else {
    return amount?.indexOf('-') > -1 ? `-${formatNumber(minusSign, decimal)}` : `+${formatNumber(minusSign, decimal)}`
    // }
  }
  const getImage = (type: string) => {
    switch (type) {
      case 'Swap':
        return icon_swap
      case 'swap_a2b':
        return icon_swap
      case 'swap_b2a':
        return icon_swap
      case 'SwapAndUnstake':
        return icon_swap
      case 'SwapAndWithdraw':
        return icon_swap
      case 'SwapAndSupply':
        return icon_swap
      case 'WithdrawAndSwap':
        return icon_swap
      case 'Unstake':
        return icon_swap
      case 'AddLiquidity':
        return icon_liquidityincreased
      case 'AddLiquidityAndClaim':
        return icon_liquidityincreased
      case 'RemoveLiquidity':
        return icon_liquidityremoved
      case 'RemoveLiquidityAndClaim':
        return icon_liquidityremoved
      case 'convert':
        return icon_swap
      case 'Contract Interaction':
        return icon_presalesettled
      case 'redeem_lock':
        return icon_redeem
      case 'mint_and_convert':
        return icon_presalesettled
      case 'collect_fee':
        return icon_presalesettled
      case 'claim_target_coin':
        return icon_liquidityremoved
      case 'create_indexer_and_place_limit_order':
        return icon_createlimitorder
      case 'place_limit_order':
        return icon_createlimitorder
      case 'cancel_order_by_owner':
        return icon_cancel
      default:
        return icon_presalesettled
    }
  }
  const getType = (type: string) => {
    switch (type) {
      case 'convert':
        return 'Convert'
      case 'RemoveLiquidity':
        return 'Remove Liquidity'
      case 'AddLiquidity':
        return 'Add Liquidity'
      case 'redeem_lock':
        return 'Redeem'
      case 'cancel_redeem_lock':
        return 'Cancel Redemption'
      case 'mint_and_convert':
        return 'Mint and Convert'
      case 'collect_fee':
        return 'Claim Fees'
      case 'redeem':
        return 'Claim (Redeem)'
      case 'claim':
        return 'Claim (Purchase)'
      case 'purchase':
        return 'Purchase'
      case 'create_and_purchase':
        return 'Purchase'
      case 'collect_reward':
        return 'Claim Farming Rewards'
      case 'open_position_with_liquidity_with_all':
        return 'Add Liquidity Failed'
      case 'RemoveLiquidityAndClaim':
        return 'Remove Liquidity'
      case 'AddLiquidityAndClaim':
        return 'Add Liquidity'
      case 'create_indexer_and_place_limit_order':
        return 'Create Order'
      case 'place_limit_order':
        return 'Create Order'
      case 'cancel_order_by_owner':
        return 'Cance Order'
      case 'claim_target_coin':
        return 'Claim Order'
      case 'swap_a2b':
        return 'Swap'
      case 'swap_b2a':
        return 'Swap'
      case 'SwapAndUnstake':
        return 'Swap'
      case 'SwapAndWithdraw':
        return 'Swap'
      case 'SwapAndSupply':
        return 'Swap'
      case 'WithdrawAndSwap':
        return 'Swap'
      case 'Unstake':
        return 'Swap'
      default:
        return type
    }
  }

  return {
    getWalletHis,
    nextCursor,
    historyList,
    historyLoading,
    getType,
    getImage,
    getAmount
  }
}
