import { useAccountServiceName } from '@cetus/hooks/src/useAccountServiceName'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useTrack from '@cetus/hooks/src/useTrack'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useSdkStore } from '@cetus/sdk-factory'
import { useAccountStore } from '@cetus/stores'
import useGlobalStore from '@cetus/stores/src/global'
import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { suiWalletList } from '@cetus/types'
import Icon from '@cetus/ui-kit/src/components/Icon'
import { addressAbridge } from '@cetus/utils'
import { Button, HStack, Image, Popover, PopoverContent, PopoverTrigger, Text, useDisclosure, VStack } from '@chakra-ui/react'
import { useCurrentAccount, useCurrentWallet, useDisconnectWallet } from '@mysten/dapp-kit'
import { useEffect, useMemo, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAccountBalance } from '../../../../hooks/src/useAccountBalance'
import { CopyButton } from '../common'
import AccountSwitch from './AccountSwitch'
import RecentTransactions from './RecentTransactions'
import usePhantomAutoConnect from './provider/usePhantomAutoConnect'

export default function ConnectButton({ isCetusPump }: { isCetusPump: boolean }) {
  usePhantomAutoConnect()
  const [isOpenRecentTrans, setIsOpenRecentTrans] = useState<boolean>(false)
  const { currentAccount, accounts, onWalletModal, saveAccounts } = useAccountStore()
  const { currentWallet, connectionStatus } = useCurrentWallet()
  const account = useCurrentAccount()
  const { mutate: disconnect } = useDisconnectWallet()
  const { getExplorerUrl } = useExplorer()
  const { isApp } = useWindowWidth()
  const navigate = useNavigate()
  const { fetchAccountBalance } = useAccountBalance()
  const { isOpen, onToggle, onClose } = useDisclosure()
  const { currentNode } = useRpcConfigStore()
  const { fetchServiceNames } = useAccountServiceName()
  const { phantomInstance } = useGlobalStore()
  const { toTrackWalletConnect } = useTrack()
  const { isInitialized } = useSdkStore()

  const { saveCurrentAccount, accountServiceNameObj, setAccountServiceNameObj } = useAccountStore()

  // ‰øùÂ≠òÂΩìÂâçÈí±ÂåÖË¥¶Êà∑
  useEffect(() => {
    // TODO  ÂÖ®Â±Ä‰øÆÊîπÈí±ÂåÖË¥¶Êà∑Âú∞ÂùÄ„ÄÇÁî®‰∫éËØªÂèñÊï∞ÊçÆÊµãËØï
    // const testAccount = {
    //   address: '0xd2d8837070eaae97308d219d080cb521a9cfa594f37f683aa5418828b9ffa2b6',
    //   publicKey: [],
    //   chains: ['sui:mainnet']
    // }
    // saveCurrentAccount(testAccount as any)

    saveCurrentAccount(account || undefined)

    if (account) {
      fetchServiceNames([account.address])
    }

    if (account && isInitialized) {
      fetchAccountBalance(account.address, true)
    }
  }, [account, isInitialized])

  useEffect(() => {
    const accounts = currentWallet?.accounts || []
    saveAccounts([...accounts])
  }, [currentWallet])

  useEffect(() => {
    if (account?.address && currentWallet?.name) {
      toTrackWalletConnect({
        walletAddress: account?.address,
        walletName: currentWallet?.name
      })
    }
  }, [account?.address, currentWallet?.name])

  const walletIcon = useMemo(() => {
    if (currentWallet) {
      const info = suiWalletList.find(wallet => currentWallet.name === wallet.walletName)
      if (info) {
        return info.icon.startsWith('http') ? info.icon : `/images/wallet/${info.icon}.png`
      }
      return currentWallet.icon
    }
    return undefined
  }, [currentWallet])

  return (
    <HStack>
      {!currentAccount && (
        <Button
          borderRadius="8px"
          fontSize="14px"
          // w="136px"
          p="0 16px"
          h="36px"
          fontWeight="500"
          onClick={() => {
            onWalletModal(true)
            onClose()
          }}
        >
          Connect
        </Button>
      )}

      {currentAccount && (
        <Popover isLazy isOpen={isOpen} onClose={onClose}>
          <PopoverTrigger>
            <Button
              variant="unstyled"
              display="flex"
              gap="8px"
              cursor="pointer"
              borderRadius="8px"
              border="1px solid"
              borderColor="border"
              p={{ base: '0 8px', lg: '0 16px' }}
              bg="block_color_opacity.50"
              backdropFilter="blur(20px)"
              h="36px"
              sx={{
                _hover: {
                  bg: 'button_outline_hov_bg'
                }
              }}
              onClick={onToggle}
            >
              <Image src={walletIcon} alt="SVG Image" boxSize="20px" objectFit="cover" borderRadius="4px" />
              <Text fontSize="14px" color="text_caption" fontWeight="500">
                {accountServiceNameObj?.[currentAccount.address] || addressAbridge(currentAccount.address)}
              </Text>
            </Button>
          </PopoverTrigger>
          {(isOpenRecentTrans ? true : isOpen) && (
            <PopoverContent w="360px" bg="bg_secondary" borderRadius="12px" p="0px">
              <VStack alignItems="start" p="16px" w="100%">
                <Text fontSize="14px" color="text_caption">
                  Connected with {currentWallet?.name}
                </Text>
                <VStack
                  mt="8px"
                  w="100%"
                  p="20px"
                  borderRadius="12px"
                  alignItems="start"
                  sx={{
                    backgroundImage: "url('/images/bg@2x.png')",
                    backgroundPosition: 'top 0px',
                    backgroundSize: '328px 116px',
                    backgroundRepeat: 'no-repeat'
                  }}
                >
                  <HStack gap="8px">
                    <Image src={walletIcon} alt="SVG Image" boxSize="30px" objectFit="cover" borderRadius="8px" />
                    <Text fontSize="14px" fontWeight="500">
                      {currentAccount?.label}
                    </Text>
                  </HStack>

                  <HStack w="100%" justifyContent="space-between" alignItems="center" mt="8px">
                    {/* Â±ïÁ§∫Èí±ÂåÖÂú∞ÂùÄ */}
                    <AccountSwitch currentAccount={currentAccount} accounts={accounts} onClose={onClose} />
                    {/* Êã∑Ë¥ùÂíåË∑≥ËΩ¨ */}
                    <HStack gap="4px">
                      <CopyButton svgW="24px" svgH="24px" text={currentAccount.address} />
                      <Icon
                        xlinkHref="#icon-icon_link1"
                        w="24px"
                        h="24px"
                        svgW="24px"
                        svgH="24px"
                        onClick={() => {
                          window.open(getExplorerUrl(currentAccount.address), '_blank')
                        }}
                      />
                    </HStack>
                  </HStack>
                </VStack>

                {isCetusPump && (
                  <Button
                    variant="outline"
                    fontSize="14px"
                    w="100%"
                    mt="12px"
                    onClick={() => {
                      onClose()
                      navigate(`/profile`)
                    }}
                  >
                    Profile
                  </Button>
                )}

                <HStack w="100%" mt="8px">
                  {/* ÊúÄËøë‰∫§ÊòìËÆ∞ÂΩï */}
                  <HStack
                    cursor="pointer"
                    sx={{
                      _hover: {
                        p: {
                          color: 'text_caption'
                        },
                        svg: {
                          fill: 'text_caption'
                        }
                      }
                    }}
                    onClick={() => {
                      setIsOpenRecentTrans(true)
                    }}
                  >
                    <Icon xlinkHref="#icon-icon_recenttransactions" svgW="20px" svgH="20px" />
                    <Text>Recent Transactions</Text>
                  </HStack>

                  {/* Êñ≠ÂºÄÈìæÊé• */}
                  <HStack
                    pl="16px"
                    cursor="pointer"
                    sx={{
                      _hover: {
                        p: {
                          color: 'text_caption'
                        },
                        svg: {
                          fill: 'text_caption'
                        }
                      }
                    }}
                    onClick={() => {
                      disconnect()
                      if (phantomInstance) {
                        phantomInstance.hide()
                      }

                      onClose()
                    }}
                  >
                    <Icon xlinkHref="#icon-icon_disconnect" svgW="20px" svgH="20px" svgHover="text_paragraph" />
                    <Text>Disconnect</Text>
                  </HStack>
                </HStack>
              </VStack>
            </PopoverContent>
          )}
        </Popover>
      )}
      {isOpenRecentTrans && <RecentTransactions isOpen={isOpenRecentTrans} onClose={() => setIsOpenRecentTrans(false)} />}
    </HStack>
  )
}
