import { useSdk } from '@cetus/sdk-factory'
import { useAccountStore } from '@cetus/stores'

export function useAccountServiceName() {
  const clmmSdk = useSdk('clmm')
  const { setAccountServiceNameObj, accountServiceNameObj } = useAccountStore()
  const fetchServiceNames = async (address: string[]) => {
    if (address?.length < 1) return
    try {
      const requests = address.map(async item => {
        const res: any = await clmmSdk!.FullClient.resolveNameServiceNames({ address: item })
        return {
          address: item,
          name: res?.data?.[0] || ''
        }
      })

      const res = await Promise.all(requests)
      console.log('üöÄ ~ fetchServiceName ~ res:', res)

      const namesObj = Object.fromEntries(res?.map(item => [item.address, item.name]))
      console.log('üöÄ ~ fetchServiceNames ~ namesObj:', namesObj)
      setAccountServiceNameObj(namesObj)
    } catch (error) {
      console.log('üöÄ ~ fetchServiceName ~ error:', error)
    }
  }

  const getServiceNames = async (accounts: any) => {
    console.log('üöÄ ~ getServiceNames ~ accounts:', accounts)
    const haveFetchAddress: string[] = []

    accounts?.forEach((item: any) => {
      const address = item?.address
      if (!accountServiceNameObj?.[address]) {
        haveFetchAddress.push(address)
      }
    })

    console.log('üöÄ ~ getServiceNames ~ haveFetchAddress:', haveFetchAddress)

    if (haveFetchAddress?.length > 0) {
      fetchServiceNames(haveFetchAddress)
    }
  }

  return {
    fetchServiceNames,
    getServiceNames
  }
}
